<script>
  import { tick } from "svelte";

  import Chart from "./Chart.svelte";
  import ScenarioPicker from "./ScenarioPicker.svelte";

  let angular;
  let angularTotal;
  let blazor;
  let blazorTotal;
  let react;
  let reactTotal;
  let vue;
  let vueTotal;
  let vue3;
  let vue3Total;
  let svelte;
  let svelteTotal;
  let labels;

  let loading = true;

  async function onChange(event) {
    loading = true;
    await tick();
    const data = event.detail;
    angular = Object.values(
      data.find((item) => item.Name === "angular_script")
    );
    angularTotal = Object.values(
      data.find((item) => item.Name === "angular_total")
    );
    blazor = Object.values(data.find((item) => item.Name === "blazor_script"));
    blazorTotal = Object.values(
      data.find((item) => item.Name === "blazor_total")
    );
    react = Object.values(data.find((item) => item.Name === "react_script"));
    reactTotal = Object.values(
      data.find((item) => item.Name === "react_total")
    );
    vue = Object.values(data.find((item) => item.Name === "vue_script"));
    vueTotal = Object.values(data.find((item) => item.Name === "vue_total"));
    vue3 = Object.values(data.find((item) => item.Name === "vue3_script"));
    vue3Total = Object.values(data.find((item) => item.Name === "vue3_total"));
    svelte = Object.values(data.find((item) => item.Name === "svelte_script"));
    svelteTotal = Object.values(
      data.find((item) => item.Name === "svelte_total")
    );

    labels = Object.keys(data[0]).filter((label) => label !== "Name");
    loading = false;
  }
</script>

<main>
  <div>
    <ScenarioPicker on:change={(event) => onChange(event)} />
    {#if !loading}
      <div class="chart-wrapper">
        <Chart
          {labels}
          {angular}
          {blazor}
          {react}
          {vue}
          {vue3}
          {svelte}
          id="canvas-scripts"
          title="Time taken to execute scripts (ms)"
        />
      </div>
      <div class="chart-wrapper">
        <Chart
          {labels}
          angular={angularTotal}
          blazor={blazorTotal}
          react={reactTotal}
          vue={vueTotal}
          vue3={vue3Total}
          svelte={svelteTotal}
          id="canvas-total"
          title="Total time to render(ms)"
        />
      </div>
    {/if}
  </div>
</main>

<style>
  .chart-wrapper {
    margin: 25px 0;
    max-width: 1400px;
  }
</style>
