<script>
  import { createEventDispatcher } from "svelte";

  import Group1AddOne from "../../assets/group1_add_one_times.csv";
  import Group1CreateComponents from "../../assets/group1_create_components_times.csv";
  import Group1CreateElements from "../../assets/group1_create_elements_times.csv";
  import Group1DeleteElements from "../../assets/group1_delete_components_times.csv";
  import Group2UpdateAll from "../../assets/group2_update_all_times.csv";
  import Group2UpdateChildRows from "../../assets/group2_update_child_rows_times.csv";
  import Group2UpdateChild from "../../assets/group2_update_child_times.csv";
  import Group2UpdateParent from "../../assets/group2_update_parent_times.csv";
  import Group3UpdateRoot from "../../assets/group3_update_root_times.csv";
  import Group3UpdateChild from "../../assets/group3_update_child_times.csv";
  import Group4UpdateAll from "../../assets/group4_update_all_times.csv";
  import Group4UpdateSingle from "../../assets/group4_update_single_times.csv";

  const dispatch = createEventDispatcher();

  function onSelect(event) {
    switch (event.target.value) {
      case "group1_create_components":
        dispatch("change", Group1AddOne);
        return;
      case "group1_create_elements":
        dispatch("change", Group1CreateComponents);
        return;
      case "group1_delete_components":
        dispatch("change", Group1CreateElements);
        return;
      case "group1_add_one":
        dispatch("change", Group1DeleteElements);
        return;
      case "group2_update_parent":
        dispatch("change", Group2UpdateParent);
        return;
      case "group2_update_child":
        dispatch("change", Group2UpdateChild);
        return;
      case "group2_update_all":
        dispatch("change", Group2UpdateAll);
        return;
      case "group2_update_child_rows":
        dispatch("change", Group2UpdateChildRows);
        return;
      case "group3_update_child":
        dispatch("change", Group3UpdateChild);
        return;
      case "group3_update_root":
        dispatch("change", Group3UpdateRoot);
        return;
      case "group4_update_single":
        dispatch("change", Group4UpdateSingle);
        return;
      case "group4_update_all":
        dispatch("change", Group4UpdateAll);
        return;
    }
  }
</script>

<div>
  <label for="scenario-select">Select scenario</label>
  <select id="scenario-select" on:change={(event) => onSelect(event)}>
    <option value="group1_create_components">group1_create_components</option>
    <option value="group1_create_elements">group1_create_elements</option>
    <option value="group1_delete_components">group1_delete_components</option>
    <option value="group1_add_one">group1_add_one</option>
    <option value="group2_update_parent">group2_update_parent</option>
    <option value="group2_update_child">group2_update_child</option>
    <option value="group2_update_all">group2_update_all</option>
    <option value="group2_update_child_rows">group2_update_child_rows</option>
    <option value="group3_update_child">group3_update_child</option>
    <option value="group3_update_root">group3_update_root</option>
    <option value="group4_update_single">group4_update_single</option>
    <option value="group4_update_all">group4_update_all</option>
  </select>
</div>
